.text-center {
  text-align: center; }

.header {
  background-color: #AA0601;
  padding: 0 88px 0 88px; }

.menu a {
  padding: 38px 32px;
  color: inherit;
  font-size: 1.25rem; }
  .menu a:hover {
    background-color: #9F1213; }

@media (max-width: 768px) {
  .header {
    padding: 0 39px 0 39px; }
    .header nav {
      width: 100%; }
  .menu a {
    padding: 38px 20px;
    color: inherit;
    font-size: 1.25rem; } }

@media (max-width: 375px) {
  .header {
    padding: 0; }
  .logo {
    padding-top: 10px;
    padding-bottom: 10px; }
  .menu li {
    width: 50%;
    border: #ffffff 1px solid; }
  .menu a {
    padding: 8px 0;
    color: inherit;
    font-size: 1.25rem; } }

.footer {
  background-color: #AA0601;
  padding: 32px 0 40px 0; }
  .footer a {
    color: inherit; }
    .footer a:hover {
      background-color: #9F1213;
      box-shadow: 3px 3px 2px #AA4440; }

.footer-content {
  border-bottom: solid 1px #ffffff; }

.footer-menu li:not(:last-child) {
  margin-right: 48px; }

.footer-icon img {
  width: 48px; }

.footer-icon li:not(:last-child) {
  margin-right: 32px; }

.footer-contact {
  width: 100%;
  line-height: 54px;
  font-size: 36px; }
  .footer-contact a {
    display: inline-block; }
  .footer-contact img {
    display: inline-block;
    width: 36px; }
    .footer-contact img:first-child {
      margin-right: 22px; }
    .footer-contact img:last-child {
      margin-right: 20px; }

@media (max-width: 768px) {
  .footer {
    padding: 32px 0; }
  .footer-menu li:not(:last-child) {
    margin-right: 40px; }
  .footer-icon img {
    width: 24px; }
  .footer-icon li:not(:last-child) {
    margin-right: 24px; }
  .footer-contact {
    font-size: 1.25rem;
    line-height: 30px; }
    .footer-contact img {
      width: 18px; }
      .footer-contact img:first-child {
        margin-right: 8px; }
      .footer-contact img:last-child {
        margin-right: 10px; } }

@media (max-width: 375px) {
  .footer {
    padding: 24px 0; }
  .footer-menu {
    display: none; }
  .footer-contact {
    width: 50%;
    font-size: 1rem; }
  .footer-declaration {
    font-size: 14px; } }

.banner {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/banner.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 840px; }

.profession li {
  width: 23%; }

.profession img {
  margin: 0 auto;
  padding-bottom: 25px; }

.profession h4 {
  font-weight: bold;
  margin-bottom: 8px; }

.profession p {
  text-align: left; }

.product .pic {
  width: 635px; }

.product .h2 img {
  display: inline-block; }

.design {
  background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/bg.png");
  background-repeat: no-repeat;
  background-position: center; }
  .design li {
    width: 49%;
    position: relative; }
    .design li:before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(255, 255, 255, 0.5); }
    .design li p {
      font-size: 72px;
      line-height: 108px;
      position: absolute;
      top: 50%;
      left: 50%;
      min-width: 100%;
      transform: translate(-50%, -50%); }
